import ourColors from "../inc/fitnessColors"
import {link} from "@wordpress/icons"
import { ToolbarGroup, ToolbarButton, Popover , Button, PanelBody,PanelRow, ColorPalette} from "@wordpress/components"
import {RichText, InspectorControls, BlockControls, __experimentalLinkControl as LinkControl, getColorObjectByColorValue} from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"
import { useState } from "@wordpress/element"


registerBlockType("fitnessblocktheme/fitnesschooselink",{
    title:"Fitness Choose Link",
    attributes:{
        text:{type:"string"},
        linkObject:{type:"object",default:{url:""}},
        icon:"<i className='fa-solid fa-arrow-right-long ms-2'></i>",
    },
    edit:EditComponent,
    save:SaveComponent
})
function EditComponent(props){
    const [isLinkPickerVisible, setIsLinkPickerVisible]=useState(false);

    function handleTextChange(x){
        props.setAttributes({text:x});
    }

    function buttonHandler(){
        setIsLinkPickerVisible(prev => !prev)
    }

    function handleLinkChange(newLink){
        props.setAttributes({linkObject:newLink})
    }
    
    
       
    return (
        <> 
            <BlockControls>
                <ToolbarGroup>
                    <ToolbarButton onClick={buttonHandler} icon={link}/>
                </ToolbarGroup>
            </BlockControls>
            
            <RichText allowedFormats={[]} tagName="a" className={`start-btn`} value={props.attributes.text} onChange={handleTextChange}/>
            
            {isLinkPickerVisible && (
                <Popover position="bottom center" onFocusOutside={() => setIsLinkPickerVisible(false)}>
                    <LinkControl settings={[]}  value={props.attributes.linkObject} onChange={handleLinkChange}/>
                    <button className="btn btn-primary" variant="primary" onClick={()=>setIsLinkPickerVisible(false)} style={{display:"block",width:"100%"}}>Save Link</button>
                </Popover>
            )}
        </>
    );
}
function SaveComponent(props){
    return (<a href={props.attributes.linkObject.url} className={`start-btn`}>{props.attributes.text}<i className='fa-solid fa-arrow-right-long ms-2'></i></a>)
}